import React, { useState, useEffect } from 'react';
import { hot } from 'react-hot-loader';
import {
  Container, Header, Button, Form, Divider, List, Segment,
} from 'semantic-ui-react';
import Number from 'react-number-format';
import { Contract, utils, BigNumber } from 'ethers';
import moment from 'moment';
import { toast } from 'react-toastify';
import Page from '../components/Page';
import LinkExternal from '../components/Link/LinkExternal';
import EtherscanLink from '../components/Link/EtherscanLink';
import { useEagerConnect, useActiveWeb3React } from '../hooks/index';

import { useSelector, useDispatch } from 'react-redux';
import * as S from '../store/selectors';
import * as A from '../store/actions';

import {
  FancyNftHeroesS1Address,
  FancyNftCubeFacetsAddress,
  FancyNftMarketplaceHeroesAddress, FancyNftMarketplaceItemsAddress,
  nullAddress,
} from '../constants';
import nftHeroesAbi from '../abis/FancyNftHeroesS1Abi.json';
import FancyNftCubeFacetsAbi from '../abis/FancyNftCubeFacetsAbi.json';
import FancyNftMarketplaceAbi from '../abis/FancyNftMarketplaceAbi.json';



function NftMarketplacePage() {
  const triedEager = useEagerConnect();
  const context = useActiveWeb3React();
  const { library, chainId, account, active } = context;

  const getNft1Contract = (withSigner = false) => {
    let provider = library;
    if (withSigner) provider = library.getSigner();
    return new Contract(FancyNftHeroesS1Address, nftHeroesAbi, provider);
  }
  const getNft2Contract = (withSigner = false) => {
    let provider = library;
    if (withSigner) provider = library.getSigner();
    return new Contract(FancyNftCubeFacetsAddress, FancyNftCubeFacetsAbi, provider);
  }
  const getMarketplace1Contract = (withSigner = false) => {
    let provider = library;
    if (withSigner) provider = library.getSigner();
    return new Contract(FancyNftMarketplaceHeroesAddress, FancyNftMarketplaceAbi, provider);
  }
  const getMarketplace2Contract = (withSigner = false) => {
    let provider = library;
    if (withSigner) provider = library.getSigner();
    return new Contract(FancyNftMarketplaceItemsAddress, FancyNftMarketplaceAbi, provider);
  }

  // Marketplace
  const [owner1, setOwner1] = useState(nullAddress);
  const [owner2, setOwner2] = useState(nullAddress);
  const [offersMaxIndex1, setOffersMaxIndex1] = useState(0);
  const [offersMaxIndex2, setOffersMaxIndex2] = useState(0);
  const [offers1, setOffers1] = useState([]);
  const [offers2, setOffers2] = useState([]);

  // mints
  const [balanceNft1, setBalanceNft1] = useState(0);
  const [balanceNft2, setBalanceNft2] = useState(0);
  const [tokenIds1, setTokenIds1] = useState([]);
  const [tokenIds2, setTokenIds2] = useState([]);


  // update
  const [initDone, setInitDone] = useState(false);
  const [updating, setUpdating] = useState(false);
  const update = async () => {
    if (updating) return;
    setUpdating(true);
    try {
      const contractNft1 = getNft1Contract();
      const contractNft2 = getNft2Contract();
      const contractMarketplace1 = getMarketplace1Contract();
      const contractMarketplace2 = getMarketplace2Contract();


      // address
      const owner1 = await contractMarketplace1.owner();
      setOwner1(owner1);
      const owner2 = await contractMarketplace2.owner();
      setOwner2(owner2);

      const offersMaxIndex1 = await contractMarketplace1.offersMaxIndex();
      setOffersMaxIndex1(offersMaxIndex1.toString());
      const offersMaxIndex2 = await contractMarketplace2.offersMaxIndex();
      setOffersMaxIndex2(offersMaxIndex2.toString());


      const offers1 = [];
      for (let i = 0; i < offersMaxIndex1; i++) {
        const offer = await contractMarketplace1.offers(i);
        if (offer.isForSale) offers1.push(offer);
      }
      setOffers1(offers1);
      const offers2 = [];
      for (let i = 0; i < offersMaxIndex2; i++) {
        const offer = await contractMarketplace2.offers(i);
        if (offer.isForSale) offers2.push(offer);
      }
      setOffers2(offers2);

      if (account) {
        let balanceNft1 = await contractNft1.balanceOf(account);
        balanceNft1 = balanceNft1.toNumber();
        setBalanceNft1(balanceNft1);
        let balanceNft2 = await contractNft2.balanceOf(account);
        balanceNft2 = balanceNft2.toNumber();
        setBalanceNft2(balanceNft2);

        const tokenIds1 = [];
        for (let i = 0; i < balanceNft1; i++) {
          let tokenId = await contractNft1.tokenOfOwnerByIndex(account, i);
          tokenIds1.push(tokenId);
        }
        setTokenIds1(tokenIds1);
        const tokenIds2 = [];
        for (let i = 0; i < balanceNft2; i++) {
          let tokenId = await contractNft2.tokenOfOwnerByIndex(account, i);
          tokenIds2.push(tokenId);
        }
        setTokenIds2(tokenIds2);

      } else {
        setBalanceNft1(0);
        setBalanceNft2(0);
        setTokenIds1([]);
        setTokenIds2([]);
      }

    } catch (error) { console.error(error); }
    setInitDone(true);
    setUpdating(false);
  }

  const [blockNumber, setBlockNumber] = useState(-1);
  const [time, setTime] = useState(0);
  useEffect(() => {
    if (library) {
      library.on('block', (blockNumber) => {
        setBlockNumber(blockNumber);
        update();
      });
    }

    setTime(Date.now());
    const intervalId = setInterval(() => {
      setTime(Date.now());
    }, 1 * 1000);

    return () => {
      clearInterval(intervalId);
    };
  }, []);
  useEffect(() => {
    if (library) {
      library.on('block', (blockNumber) => {
        setBlockNumber(blockNumber);
        update();
      });
    }
  }, [library]);

  useEffect(() => {
  }, [time]);

  const [nft1TokenIdForSale, setNft1TokenIdForSale] = useState('');
  const [nft2TokenIdForSale, setNft2TokenIdForSale] = useState('');
  const [nft1PriceForSale, setNft1PriceForSale] = useState('');
  const [nft2PriceForSale, setNft2PriceForSale] = useState('');
  const onChangeValue = (e, { name, value }) => {
    switch (name) {
      case 'nft1TokenIdForSale': setNft1TokenIdForSale(value); break;
      case 'nft2TokenIdForSale': setNft2TokenIdForSale(value); break;
      case 'nft1PriceForSale': setNft1PriceForSale(value); break;
      case 'nft2PriceForSale': setNft2PriceForSale(value); break;
      default: break;
    }
  }


  const doTx = async (promiseTx) => {
    try {
      const tx = await promiseTx;
      if (!tx) return;
      toast.info('Pending tx doTx', tx.hash);
      await tx.wait(1);
      toast.success('Done tx doTx', tx.hash);
    } catch (error) {
      console.error(error);
      toast.error('Error doTx');
    }
  }


  const nft1OfferForSale = async () => {
    if (nft1TokenIdForSale.length === 0) return;
    const contract = getMarketplace1Contract(true);
    await doTx(contract.offerNftForSale(
      nft1TokenIdForSale, utils.parseEther(nft1PriceForSale)
    ));
  }
  const nft2OfferForSale = async () => {
    if (nft2TokenIdForSale.length === 0) return;
    const contract = getMarketplace2Contract(true);
    await doTx(contract.offerNftForSale(
      nft2TokenIdForSale, utils.parseEther(nft2PriceForSale)
    ));
  }



  const parentId = useSelector(S.ref.getParentId);
  const dispatch = useDispatch();
  const [refId, setRefId] = useState(0);
  useEffect(async () => {
    // не в дереве
    if (parentId) {
      setRefId(parentId);
      dispatch(A.ref.setParentId(parentId));
    }
  }, [parentId]);

  const buyNft1 = async (tokenId, minValue) => {
    const contract = getMarketplace1Contract(true);
    await doTx(contract.buyNft(
      tokenId, refId,
      { value: minValue }
    ));
  }

  const buyNft2 = async (tokenId, minValue) => {
    const contract = getMarketplace1Contract(true);
    await doTx(contract.buyNft(
      tokenId, refId,
      { value: minValue }
    ));
  }

  return (
    <Page title={'Marketplace'}>
      <Container>
        <br />
        <Segment>
          <Header as='h1'>
            <Header.Content>
              Marketplace
              <Header.Subheader>
                <div>Smart Contract for Heroes: <EtherscanLink address={FancyNftMarketplaceHeroesAddress}>{FancyNftMarketplaceHeroesAddress}</EtherscanLink></div>
                <div>Owner: <EtherscanLink address={owner1}>{owner1}</EtherscanLink></div>
                <Divider />
                <div>Smart Contract for Items: <EtherscanLink address={FancyNftMarketplaceItemsAddress}>{FancyNftMarketplaceItemsAddress}</EtherscanLink></div>
                <div>Owner: <EtherscanLink address={owner2}>{owner2}</EtherscanLink></div>
                <Divider />
                <div>Last Block: <Number value={blockNumber}
                  displayType='text' thousandSeparator />
                </div>
              </Header.Subheader>
            </Header.Content>
          </Header>
        </Segment>


        <Segment>
          <div>Address: <EtherscanLink address={account}>{account}</EtherscanLink></div>
          <div>Balance NFT (Heroes): <EtherscanLink token={`${FancyNftHeroesS1Address}?a=${account}`}><Number value={balanceNft1} displayType='text' thousandSeparator fixedDecimalScale decimalScale={0} /></EtherscanLink></div>
          <div>tokenIds: {tokenIds1.join(', ')}</div>
          <div>Balance NFT (Items): <EtherscanLink token={`${FancyNftCubeFacetsAddress}?a=${account}`}><Number value={balanceNft2} displayType='text' thousandSeparator fixedDecimalScale decimalScale={0} /></EtherscanLink></div>
          <div>tokenIds: {tokenIds2.join(', ')}</div>
        </Segment>

        <Segment>
          <div>Heroes on Sale: <Number value={offersMaxIndex1} displayType='text' thousandSeparator /></div>
          <div>Items on Sale: <Number value={offersMaxIndex2} displayType='text' thousandSeparator /></div>
        </Segment>


        <Segment>
          <Header>Heroes Offer For Sale</Header>
          <Form>
            <Form.Input
              name='nft1TokenIdForSale' label={'TokenId'} type='number' required
              value={nft1TokenIdForSale} onChange={onChangeValue}
            />
            <Form.Input
              name='nft1PriceForSale' label={'Price'} type='number' required
              value={nft1PriceForSale} onChange={onChangeValue}
            />
            <Button primary onClick={nft1OfferForSale}>Heroes Offer For Sale</Button>
          </Form>
        </Segment>

        <Segment>
          <Header>Items Offer For Sale</Header>
          <Form>
            <Form.Input
              name='nft2TokenIdForSale' label={'TokenId'} type='number' required
              value={nft1TokenIdForSale} onChange={onChangeValue}
            />
            <Form.Input
              name='nft2PriceForSale' label={'Price'} type='number' required
              value={nft2PriceForSale} onChange={onChangeValue}
            />
            <Button primary onClick={nft2OfferForSale}>Items Offer For Sale</Button>
          </Form>
        </Segment>


        <Segment>
          <div>Heroes on Sale:</div>
          <List>
            {offers1.map((item, index) => {
              if (!item.isForSale) return null;
              return (<List.Item key={index}>
                <Segment>
                  <div>tokenId: {item.tokenId.toNumber()}</div>
                  <div>Price: <Number value={utils.formatEther(item.minValue)} suffix=' BNB' displayType='text' thousandSeparator /></div>
                  <div>Seller: <EtherscanLink address={item.seller}>{item.seller}</EtherscanLink></div>
                  <Button primary onClick={() => buyNft1(item.tokenId, item.minValue)}>buyNft</Button>
                </Segment>
              </List.Item>);
            })}
          </List>
        </Segment>

        <Segment>
          <div>Items on Sale:</div>
          <List>
            {offers2.map((item, index) => {
              if (!item.isForSale) return null;
              return (<List.Item key={index}>
                <Segment>
                  <div>tokenId: {item.tokenId.toNumber()}</div>
                  <div>Price: <Number value={utils.formatEther(item.minValue)} suffix=' BNB' displayType='text' thousandSeparator /></div>
                  <div>Seller: <EtherscanLink address={item.seller}>{item.seller}</EtherscanLink></div>
                  <Button primary onClick={() => buyNft2(item.tokenId, item.minValue)}>buyNft</Button>
                </Segment>
              </List.Item>);
            })}
          </List>
        </Segment>
      </Container>
    </Page>
  );
};

export default hot(module)(NftMarketplacePage);
